.wrap {
  margin: 0px;
  padding: 0px;
}
.Search {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
}

.logo{
    display: inline-block;
    position: relative;
    right: 50px;

}
.logo img {
  width: 350px;
  height: auto;
}

.searchengine{
    position: relative;
    right: 50px;
    top: 20px;
    height: 100%;
    text-align: right;
}
.searchengine input {
  width: 400px;
  height: 35px;
  text-align: left;
  padding-left: 20px;
  border: 5px solid rgb(69, 69, 172);
}
.searchengine button {
  position: relative;
  right: 10px;
  bottom: 44px;
  background-color: rgb(69, 69, 172);
  color: white;
  border-radius: 10px;
  width: 50px;
  padding: 8px 4px;
  border: none;
  cursor: pointer;
}
.searchengine button:hover {
  background-color: #36368a;
  color: white;
}
.logo2 {
  position: relative;
  right: 70px;
  top: 5px;
}
.logo2 img {
  width: 350px;
  height: auto;
}
.glasses {
  position: absolute;
  display: flex;
  top: 50px;
}
.lefteyeball,
.righteyeball {
  background-color: rgb(162, 174, 237);
  position: relative;
  width: 34px;
  height: 34px;
  border-radius: 50px;
  top: 4px;
  left: 162px;
  margin: 0 3px;
}
.lefteye,
.righteye {
  background-color: black;
  width: 19px;
  height: 19px;
  border-radius: 50px;
  position: relative;
  top: 8px;
  left: 8px;
}

